.app-wrapper {
  display: grid;
  margin: 0 auto;
  width: 1400px;
  grid-template-rows: 80px 6fr;
  grid-template-columns: 2fr 8fr;
  grid-gap: 5px;
  
  grid-template-areas: "header header" "nav content";
}

.app-wrapper-content {
  grid-area: content;
  background-color: cornsilk;
}